import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})
export class FormComponent implements OnInit {
 
  students : {name:string,rollNo:number, gender:string}[] = [
    {name: "Dharti", rollNo:101,gender: "Female"},
    {name: "Solanki", rollNo:102, gender: "Male" }
  ];
  txtName="";
  txtRollNo="";
  radioGender="Male";
  Myimage:[]=[];
  idToEdit=-1;
  msg: string="";
  isOk=false;
  isError=false;
  constructor() { }

  ngOnInit(): void {
  }
  AddStudent(){
    let temp={ name: this.txtName, rollNo: parseInt(this.txtRollNo), gender: this. radioGender };
    this.students.push(temp);
    this.txtName="";
    this.txtRollNo="";
    this.isOk=true;
    this.msg="Student Added";
    setTimeout(()=>{this.isOk=false},2000);
  }
  removeStudent(rollNo: number){
    this.students= this.students.filter(
    item => item. rollNo != rollNo
   );
   this.isOk=true;
    this.msg="Student Deleted";
    setTimeout(()=>{this.isOk=false},2000);
  }
  setForEdit(s:{name:string,rollNo: number, gender:string}){
    this.txtName=s.name;
    this.txtRollNo= s.rollNo + "";
    this.radioGender= s.gender;
    this.idToEdit=this.students.findIndex(i=>i.rollNo== s.rollNo);
    }
  EditStudent(){
    let temp={ name: this.txtName, rollNo: parseInt(this.txtRollNo),gender: this. radioGender};
      this.students[this.idToEdit]=temp;
      this.idToEdit=-1;
      this.txtName="";
      this.txtRollNo="";
      this.isOk=true;
      this.msg="Student Edited";
      setTimeout(()=>{this.isOk=false},2000);
    }
    SelectImage(e:any){
      var reader= new  FileReader();
      reader.onload=(f:any)=>{
         this.Myimage=f.target.result;
      }
      reader.readAsDataURL(e.target.files[0]);
      
    }
}
