import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddFacultyComponent } from './add-faculty/add-faculty.component';
import { AuthGuard } from './auth.guard';
import { DetailFacultyComponent } from './detail-faculty/detail-faculty.component';
import { EditFacultyComponent } from './edit-faculty/edit-faculty.component';
import { FacultiesComponent } from './faculties/faculties.component';
import { FormComponent } from './form/form.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';

const routes: Routes = [
  {path:"", component: HomeComponent},
  {path:"login", component: LoginComponent},
  {path:"home", component: HomeComponent},
  {path:"form", component: FormComponent},
  {path: "faculties", component: FacultiesComponent, canActivate:[AuthGuard]},
  {path : "faculty/add", component : AddFacultyComponent, canActivate:[AuthGuard]},
  {path :"faculty/edit/:id", component:EditFacultyComponent},
  {path :"faculty/:id", component:DetailFacultyComponent}
  
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
