import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiFacultyService } from '../api-faculty.service';
import { Faculty } from '../faculty';

@Component({
  selector: 'app-edit-faculty',
  templateUrl: './edit-faculty.component.html',
  styleUrls: ['./edit-faculty.component.css']
})
export class EditFacultyComponent implements OnInit {

  id=0;
  facultyDetail : Faculty = new Faculty();
  constructor(private _activatedRoute : ActivatedRoute,private _api:ApiFacultyService,private _route:Router) { }

  ngOnInit(): void {
    this.id = this._activatedRoute.snapshot.params.id;
    let ob=this._api.getFacultyById(this.id);
    ob.subscribe((res:any)=>{
      this.facultyDetail = res;
    })
  }

  editFaculty(){
    let ob = this._api.update(this.facultyDetail,this.id);
      ob.subscribe(()=>{
        this._route.navigate(['faculties']);
      });
  }

}
