import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiUserService } from '../api-user.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  UserName : string ='';
  Password : string = '';

  constructor(private _apiUser : ApiUserService,private _route : Router) { }

  ngOnInit(): void {
    
  }

  checkLogin(){
      this._apiUser.getAllUser().subscribe((res:any)=>{
        res.records.forEach((temp:{UserName:string, Password:string}) => {
          if(temp.UserName==this.UserName && temp.Password == this.Password){
            sessionStorage.setItem('un',this.UserName);
            localStorage.setItem('userInfo',temp.UserName);
            this._route.navigate(['faculties']);
          }
        });
        this.UserName='';
        this.Password='';
      })
  }

}
